import { Component, Input, OnInit, Output, EventEmitter, OnDestroy } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { ModalController } from '@ionic/angular';
import { Subscription } from 'rxjs';

import { Estados } from 'src/app/shared/consts/Estado.const';
import { LotericaDTO } from 'src/app/shared/interfaces/lotericas/LotericaDTO';
import { UserDTO } from 'src/app/shared/interfaces/users/UserDTO';
import { FuncionarioService } from 'src/app/shared/services/funcionario/funcionario.service';
import { LotericaService } from 'src/app/shared/services/loterica/loterica.service';

@Component({
  selector: 'app-update-endereco',
  templateUrl: './update-endereco.component.html',
  styleUrls: ['./update-endereco.component.scss'],
})
export class UpdateEnderecoComponent implements OnInit, OnDestroy {

  @Input()
  currentEndereco: UserDTO | LotericaDTO;

  @Input()
  isFuncionario: boolean;

  @Output()
  createdEvent: EventEmitter<any> = new EventEmitter();

  form: FormGroup;

  listEstados = Estados;

  private subscriptions = new Subscription();

  constructor(
    private formBuilder: FormBuilder,
    private funcionarioService: FuncionarioService,
    private lotericaService: LotericaService,
    private modal: ModalController,

  ) {
  }
  ngOnInit() {
    const { cep, endereco, numero, complemento, bairro, cidade, estado } = this.currentEndereco.endereco;

    this.form = this.formBuilder.group({
      cep: [cep, [Validators.required]],
      endereco: [endereco, [Validators.required]],
      numero: [numero, [Validators.required,]],
      complemento: [complemento, [Validators.required,]],
      bairro: [bairro, [Validators.required,]],
      cidade: [cidade, [Validators.required,]],
      estado: [estado, [Validators.required,]],
    });

    this.form.get("estado").setValue(this.listEstados.find(e => e.sigla === estado));
  }

  ngOnDestroy(): void {
    this.subscriptions.unsubscribe();
  }

  update() {
    const { id } = this.currentEndereco;
    const cep = this.form.get('cep').value;
    const endereco = this.form.get('endereco').value;
    const numero = this.form.get('numero').value;
    const complemento = this.form.get('complemento').value;
    const bairro = this.form.get('bairro').value;
    const cidade = this.form.get('cidade').value;
    const estado = this.form.get('estado').value.sigla;

    if (this.isFuncionario) {
      const sub = this.funcionarioService.updateEndereco({ cep, endereco, numero, complemento, estado, bairro, cidade }, id)
        .subscribe(response => {
          this.fechar(true);
        });
      this.subscriptions.add(sub);
    } else {
      const sub = this.lotericaService.updateEndereco({ cep, endereco, numero, complemento, estado, bairro, cidade }, id)
        .subscribe(response => {
          this.fechar(true);
        });
      this.subscriptions.add(sub);
    }
  }

  fechar(wasUpdated = false) {
    this.modal.dismiss(wasUpdated);
  }

  isInputError(inputName: string): boolean {
    const resp =
      this.form.controls[inputName].dirty &&
      this.form.controls[inputName].errors;

    if (resp) {
      return true;
    }
    return false;
  }

  disableBTN(): boolean {
    const situacao = !this.form.valid;

    return situacao;
  }

}
